import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router'
import './Register.css'

const Register = ({ theme, animation, setAnimation }) => {
    const history = useHistory()
    const [datos, setDatos] = useState({
        empresa: "",
        cuit: "",
        telefono: "",
        ubicacion: ""
    })
    useEffect(() => {
        const element = document.querySelector(".formulario")
        switch (animation) {
            case true:
                element.style.animation = "haciaAbajo 1s 1"
                element.style.height = "70vh"
                setAnimation(null)
                break;
            case false:
                element.style.animation = "haciaArriba 1s 1"
                setAnimation(null)
                break;
            default:
                break;
        }
    }, [animation])
    const handleInputChange = (e) => {
        setDatos({
            ...datos,
            [e.target.name]: e.target.value
        })
    }
    const handleSubmit = (event) => {
        if (datos.empresa === "" || datos.cuit === "" || datos.telefono === "" || datos.ubicacion === "") {
            alert("Hay campos sin rellenar, revise el formulario.")
        } else {
            history.push("/registered")
        }
    }
    return (<div className="registerContainer">
        <div className="col">
            <p className='titulo'>REGISTRO DE UNA NUEVA EMPRESA</p>
        </div>
        <div className="formulario">
            <div>
                <div className="col">
                    <p className='titulo'>EMPRESA</p>
                    <input type="text" placeholder="ej: CMPCP S.A." className={"form-input " + theme} onChange={handleInputChange} name="empresa"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>CUIT</p>
                    <input type="text" placeholder="ej: 11-11111111-1" className={"form-input " + theme} onChange={handleInputChange} name="cuit"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>TELEFONO</p>
                    <input type="tel" placeholder="Whatsapp" className={"form-input " + theme} onChange={handleInputChange} name="telefono"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>LOCALIDAD</p>
                    <input type="text" placeholder="ej: Rosario, Santa Fe, Argentina" className={"form-input " + theme} onChange={handleInputChange} name="ubicacion"></input>
                </div>
                <div className="col" style={{ marginTop: "20vh" }}>
                    <button style={{ padding: "20px 10px", fontSize: "2vh" }} className={"btn" + theme + " " + theme} onClick={handleSubmit}>COMPLETAR REGISTRO</button>
                </div>
            </div>
        </div>
    </div>)
}
export default Register;