import React from 'react'
import { Person, PersonPlus } from 'react-bootstrap-icons'
import { useHistory } from 'react-router'
import './Transportista.css'

export const Transportista = ({theme}) => {
    const history = useHistory()
    return (
        <div className='transpContainer'>
        <div className={"btn" + theme + " btnTransportista " + theme} onClick={() => {
            history.push("/register")
        }}>
        <PersonPlus size="3vh"></PersonPlus>
            <div size="5vh">Quiero registrarme</div>
        </div>
        <div className={"btn" + theme + " btnTransportista " + theme} onClick={() => {
            history.push("/login")
        }}>
        <Person size="3vh"></Person>
            <div size="5vh">Quiero ingresar</div>
        </div>
        </div>
    )
}
export default Transportista;
