import React, { useEffect, useState } from 'react'
import {
    BrowserRouter as Router,
    //HashRouter as Router,
    Switch,
    Route,
} from "react-router-dom";
import Home from './components/Home';
import styled, { ThemeProvider } from 'styled-components'
import { darkTheme, GlobalStyles, lightTheme } from './themes'
import './App.css'
import Register from './components/Register';
import Registered from './components/Registered';
import NavBar from './components/NavBar';
import Transportista from './components/Transportista';
import Buscar from './components/Buscar';


const App = () => {
    const back = require("./img/" + getRandomInt(1, 12) + '.jpg').default
    const [theme, setTheme] = useState('Light')
    const [animation, setAnimation] = useState(true)
    useEffect(() => {
        // window.matchMedia('(prefers-color-scheme: dark)').matches ? setTheme('Dark') : setTheme('Light')
        setTheme("Dark")
    }, [])
    const StyledApp = styled.div`
    color: ${(props) => props.theme.fontColor};
    display: flex;
    flex-direction: column;
    width: 100vw
    `;
    function getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min)) + min;
    }

    return (
        <ThemeProvider theme={theme === 'Light' ? lightTheme : darkTheme}>
            <GlobalStyles />
            <div style={{
                display: 'flex',
                position: "absolute",
                zIndex: -1,
                filter: "blur(5px)",
                backgroundImage: "url(" + back + ")",
                backgroundSize: "cover",
                width: "100vw",
                height: "100vh"
            }} />
            <div style={{ zIndex: 1,display: "flex" }}>
                <StyledApp>
                    <Router>
                        <NavBar theme={theme} setAnimation={setAnimation}></NavBar>
                        <Switch>
                        <Route exact path='/registered' component={() => <Registered theme={theme} animation={animation} setAnimation={setAnimation} />} />
                        <Route exact path='/register' component={() => <Register theme={theme} animation={animation} setAnimation={setAnimation} />} />
                        <Route exact path='/transportista' component={() => <Transportista theme={theme} />} />
                        <Route path='/buscar' component={() => <Buscar theme={theme} />} />
                        <Route path='/' component={() => <Home theme={theme} globalAnimationState={animation} setAnimation={setAnimation} />} />
                        </Switch>
                    </Router>
                </StyledApp>
            </div>
        </ThemeProvider>)
}
export default App;