import React, { useEffect, useState } from 'react'
import queryString from 'query-string'
import { useLocation } from 'react-router-dom'
import axios from 'axios'
import './Buscar.css'
import { ArrowDown, PatchCheckFill } from 'react-bootstrap-icons'


export default function Buscar() {
    /////////////////////////////////////////////////////////////////// DECLARACION VARIABLES
    const options = {
        withCredentials: true,
        headers: {
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
        }
    }
    const url = "https://validacion.hgtsa.com.ar/lappa/transportista/buscadorget" // ?loc1=2&loc2=4&pagina=3
    const [data, setData] = useState({
        origen: "",
        destino: "",
        total: 0,
        pagina: 0,
        paginas: 0,
        transportistas: [{
            id: 0,
            web: "",
            razon: "",
            nombre: "",
            telefono: "",
            email: "",
            categoria: null
        }]
    })
    const { search } = useLocation()
    const qs = queryString.parse(search)
    ///////////////////////////////////////////////////////////////////////////////////////////// DECLARACION FUNCIONES
    useEffect(() => {
        async function fetchGetData() {
            await axios.get(url + "?loc1=" + qs.origen + "&loc2=" + qs.destino + "&pagina=" + qs.pagina, { options }).then(res => {
                console.log(res.data);
                setData(res.data)
                // let tmp = []
                // res.data.map((item, index) => {
                //     return item
                // })
            });
        }
        fetchGetData()
    }, [])
    const ArmarPagina = () => {
        return (<div className="pagina">
            <div>
                <p>
                    {data.origen}
                </p>
                <ArrowDown />
                <p>
                    {data.destino}
                </p>
            </div>
            <ArmarTransportistas lista={data.transportistas} />
        </div>)
    }
    const ArmarTransportistas = ({ lista }) => {
        return <div className='transportistasList'>
            {lista.map((item, index) => {
                return <div className='card'>
                    <p className='razon'>{item.razon}</p>
                    <div className='nombre'>{item.nombre}</div>
                    <div className='telefono'>{item.telefono}</div>
                    <div className='email'>{item.email}</div>
                    {index <= 1 ? <PatchCheckFill size={"3vh"} style={{alignSelf: "flex-end",color: "lime"}}/> : undefined}
                </div>
            })}
        </div>
    }
    const ArmarPaginas = ({paginas}) => {
        return <div className="paginas">
        {}
        </div>
    }
    return (
        <div className='container'>
            <ArmarPagina />
            <ArmarPaginas  paginas={data.paginas}/>
        </div>
    )
}